//onsbumit event once submit button is clicked
const onSubmit = ({formData}, e) => {

  //see what has been submitted
  console.log("Data submitted: ",  formData);

  //Store formData to be manipulated
  var obj1 = formData;
  var filesAccept = obj1.filesAccept;
      console.log("object 1", obj1);
      console.log("filesAccept", filesAccept);

      //check file type
      console.log(typeof filesAccept);



      //if single file accept is empty then the multi fileaccept is invoked
      if (filesAccept == null){

        //process multi files
        console.log('Button clicked');
        console.log("Array of objects", formData);
        var arraysAccept = formData.files;
        console.log("Array of objects", arraysAccept[0]);
        console.log("Array of objects", arraysAccept[1]);
        console.log("Array of objects", arraysAccept[2]);
        var arraysAcceptdissected0 = arraysAccept[0].slice(54);
        var arraysAcceptdissected1 = arraysAccept[1].slice(56);
        var arraysAcceptdissected2 = arraysAccept[2].slice(54);
        console.log("arraysAcceptdissected0", arraysAcceptdissected0);
        console.log("arraysAcceptdissected1", arraysAcceptdissected1);
        console.log("arraysAcceptdissected2", arraysAcceptdissected2);
        var decodeFile0 = atob(arraysAcceptdissected0);
        var decodeFile1 = atob(arraysAcceptdissected1);
        var decodeFile2 = atob(arraysAcceptdissected2);
        console.log("decodedFile0", decodeFile0);
        console.log("decodedFile1", decodeFile1);
        console.log("decodedFile2", decodeFile2);
        const objfd = JSON.parse(decodeFile0);
        const objjs = JSON.parse(decodeFile1);
        const objus = JSON.parse(decodeFile2);

        //render multi file uploaded
        render((
          <Form
          schema={objjs}
          formData={objfd}
          uiSchema={objus}
          ></Form>),document.getElementById("root"));
          return true;
      }

  //Slice the transmitted string
    var filesAcceptdissected = filesAccept.slice(50);
    console.log("filesAcceptdissected", filesAcceptdissected);

  //Convert string to x64
    var decodeFile = atob(filesAcceptdissected);
    console.log("decodedFile", decodeFile);

  //Convert string to object to manipulate
    const obj2 = JSON.parse(decodeFile);
    console.log(typeof obj2);

  //Draw jsonschema, formData & UiSchema from the object individually
    var newJsonSchema = obj2["jsonSchema"];
    var newFormData = obj2["formData"];
    var newUiSchema = obj2["uiSchema"];
    console.log("jsonschema", newJsonSchema);
    console.log("formData", newFormData);
    console.log("UiSchema", newUiSchema);

  //Render new jsonschema, formData & UiSchema at the position of root in index.html
    render((
      <Form
        schema={newJsonSchema}
        formData={newFormData}
        uiSchema={newUiSchema}
        >
      </Form>
    ), document.getElementById("root"));
}

