            //console.log("content of getjsonstring after event", getjsonstring)
        
            //replace extraobj with the getjson content
            //extraobj = getjsonstring
         //extraobj gets thrown to the other classes further down at new extraobj 
         extraobj = JSON.parse(extraobj)
         console.log("extraobj", extraobj)
